/* Base Styles */
p {
  font-family: Arial, Helvetica, sans-serif !important;
  color: black;
}

svg {
  fill: black;
}

path {
  max-width: 40px;
  max-height: 40px;
}

#root, body, html {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.root_inner {
  width: 25%;
  height: 60%;
  padding: 2.5% 10px 5% 10px;
  display: flex;
  flex-direction: column;
  gap: 25px;
}

.root_inner header {
  display: flex;
  align-items: center;
  gap: 16px;
}

.root_inner header h1 {
  font-size: 1.5em;
  font-family: Arial, Helvetica, sans-serif;
  text-transform: capitalize;
}

.root_inner select {
  padding: 10px 30px 10px 10px;
  font-size: 1.2em;
  font-family: Arial, Helvetica, sans-serif;
  border-radius: 12px;
  border: 3px solid black;
}

.root_inner header div.square {
  width: 50px;
  height: 50px;
  background-color: black;
  border-radius: 10px;
}

button {
  cursor: pointer;
  width: 100%;
  height: 50px;
  border-radius: 7px;
  background-color: black;
  color: white;
  padding: 10px 30px 10px 10px;
  font-size: 1em;
  letter-spacing: 1px;
  font-family: Arial, Helvetica, sans-serif;
}

div.repo .infos {
  display: flex;
  justify-content: space-between;
}

.infos div {
  display: flex;
  align-items: center;
  gap: 6px;
}

.logs {
  width: 100%;
  height: 100px;
  background-color: #E9ECEF;
  border-radius: 7px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: black;
  font-size: 1.5em;
  text-transform: capitalize;
}


div.repo {
  padding: 20px 15px 20px 15px;
  flex-direction: column;
  border-radius: 14px;
  border: 3px solid black;
  text-decoration: none;
  color: inherit;
}

div.repo:hover {
  background-color: #f8f9fa;
  transition: background-color 0.3s ease;
}

a {
  text-decoration: none;
}

.circle {
  background-color: #F1E15A;
  width: 10px;
  height: 10px;
  border-radius: 50%;
}

.repo.error {
  display: none;
}

.repo p.name {
  font-size: 1.5em;
  font-weight: bold;
  text-transform: capitalize;
}

.repo p.description {
  font-size: 0.9em;
  text-transform: capitalize;
  color: #C4C4C4;
}

svg {
  width: 10px;
}

/* Responsive Styles */
@media (max-width: 1200px) {
  .root_inner {
    width: 40%;
    height: auto;
  }

  .root_inner header h1 {
    font-size: 1.3em;
  }

  .root_inner select {
    font-size: 1em;
  }

  .repo p.name {
    font-size: 1.3em;
  }

  button {
    height: 45px;
    font-size: 0.9em;
  }
}

@media (max-width: 768px) {
  .root_inner {
    width: 70%;
  }

  .root_inner header h1 {
    font-size: 1.2em;
  }

  .root_inner select {
    padding: 8px 20px;
    font-size: 1em;
  }

  button {
    height: 40px;
    padding: 8px;
    font-size: 0.9em;
  }

  .repo p.name {
    font-size: 1.2em;
  }

  .repo p.description {
    font-size: 0.8em;
  }

  .logs {
    height: 80px;
    font-size: 0.9em;
  }

  div.repo {
    padding: 15px 10px;
  }
}

@media (max-width: 480px) {
  .root_inner {
    width: 90%;
  }

  .root_inner header h1 {
    font-size: 1em;
  }

  .root_inner select {
    padding: 6px 15px;
    font-size: 0.9em;
  }

  button {
    height: 35px;
    font-size: 0.8em;
  }

  .repo p.name {
    font-size: 1em;
  }

  .repo p.description {
    font-size: 0.7em;
  }

  .logs {
    height: 70px;
    font-size: 0.8em;
  }
}

/* Add hover and focus state for accessibility */
a:hover, a:focus {
  outline: none;
  background-color: #e2e6ea;
}
